import React from 'react';
import ClientFeaturesItem from './ClientFeaturesItem';

const ClientFeatures = () => {
    return (
        <section className="h_security_area">
            <div className="container">
                <div className="hosting_title security_title text-center">
                    <h2 className="wow fadeInUp" data-wow-delay="0.2s"><span>Parcerias Estratgicas</span> para Crescimento Digital</h2>

                </div>
                <div className="row">
                    <ClientFeaturesItem itemClass="pr_70" image="security_1.png" Title="Transformao Digital para Agncias de Marketing" pText="Parcerias robustas com agncias de marketing, proporcionando a evoluo digital de seus clientes atravs do desenvolvimento de websites B2B. Elevamos a presena digital para capturar leads qualificados e maximizar as converses." />
                    <ClientFeaturesItem itemClass="pl_70" image="security_2.png" Title="E-commerce Avanado" pText="Desenvolvemos plataformas de e-commerce que transformam visitantes em compradores, atravs de uma experincia de usurio impecvel e otimizao de converso, impulsionando vendas e crescimento sustentvel." />
                    <ClientFeaturesItem itemClass="pr_70" image="security_3.png" Title="Presena Digital para Empresas e Empreendedores" pText="Criamos solues web personalizadas para empresas e empreendedores que desejam destacar-se no mercado digital. Nossos websites so projetados para refletir a essncia da marca e engajar efetivamente o pblico-alvo." />
                    <ClientFeaturesItem itemClass="pl_70" image="security_4.png" Title="Proteo e Segurana de Dados" pText="A segurana dos seus dados  nossa prioridade. Oferecemos solues que protegem informaes sensveis, garantindo a confiana e a segurana operacional para empresas digitais em todas as transaes e interaes online." />

                </div>
            </div>
        </section>
    )
}
export default ClientFeatures;