import React, { Component } from 'react';
import Slider from 'react-slick'

class Pserviceslider extends Component {
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      arrows: true,
      autoplay: true,
      autoplaySpeed: 2000,
      slidesToShow: 3,
      slidesToScroll: 1,
      centerMode: true,
      centerPadding: '100px',
      responsive: [
        {
          breakpoint: 1250,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
            centerMode: true,
            centerPadding: '100px',
          }
        },
        {
          breakpoint: 768,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
            centerMode: false,
            centerPadding: '0px',
          }
        },
        {
          breakpoint: 576,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            centerMode: false,
            centerPadding: '0px',
          }
        }
      ]
    };
    return (
      <Slider className="service_carousel" {...settings}>
        <div className="service_item">
          <div className="icon s_icon_one"><i className="ti-check"></i></div>
          <h4 className="f_600 f_size_20 l_height28 t_color2 mb_20">Desenvolvimento Web Sob Medida<br />Impulsione Seu Negcio</h4>
          <p>Transforme sua presena online com aplicaes web B2B personalizadas, projetadas para capturar leads qualificados e aumentar suas vendas de forma exponencial.</p>
          <img className="float-right" src={require('../../../img/home2/undraw.png')} alt="" />
        </div>
        <div className="service_item">
          <div className="icon s_icon_two"><i className="ti-location-arrow"></i></div>
          <h4 className="f_600 f_size_20 l_height28 t_color2 mb_20">Otimizao de Converso<br />Resultados Comprovados</h4>
          <p>Maximize a eficcia do seu site com estratgias de otimizao de converso. Atraia, engaje e converta seu pblico-alvo com mais eficincia.</p>
          <img className="float-right" src={require('../../../img/home2/inbox.png')} alt="" />
        </div>
        <div className="service_item">
          <div className="icon s_icon_three"><i className="ti-search"></i></div>
          <h4 className="f_600 f_size_20 l_height28 t_color2 mb_20">SEO e Marketing de Contedo<br />Alcance Orgnico Ampliado</h4>
          <p>Amplie seu alcance no mercado com SEO e marketing de contedo estratgico, atraindo mais visitantes qualificados e aumentando a visibilidade do seu negcio.</p>
          <img className="float-right" src={require('../../../img/home2/file.png')} alt="" />
        </div>
        <div className="service_item">
          <div className="icon s_icon_four"><i className="ti-stats-up"></i></div>
          <h4 className="f_600 f_size_20 l_height28 t_color2 mb_20">Anlise e Inteligncia de Negcios<br />Decises Baseadas em Dados</h4>
          <p>Utilize anlises avanadas e inteligncia de negcios para entender melhor seus clientes e tomar decises informadas que impulsionem o crescimento.</p>
          <img className="float-right" src={require('../../../img/home2/report.png')} alt="" />
        </div>
        <div className="service_item">
          <div className="icon s_icon_one"><i className="ti-check"></i></div>
          <h4 className="f_600 f_size_20 l_height28 t_color2 mb_20">Integrao de Sistemas<br />Fluxos de Trabalho Eficientes</h4>
          <p>Automatize e integre seus sistemas para criar fluxos de trabalho mais eficientes. Melhore a produtividade e reduza custos com solues tecnolgicas avanadas.</p>
          <img className="float-right" src={require('../../../img/home2/undraw.png')} alt="" />
        </div>
        <div className="service_item">
          <div className="icon s_icon_two"><i className="ti-location-arrow"></i></div>
          <h4 className="f_600 f_size_20 l_height28 t_color2 mb_20">Desenvolvimento gil<br />Entrega Rpida e Adaptvel</h4>
          <p>Adote metodologias geis para acelerar o desenvolvimento e a entrega de suas aplicaes web. Responda rapidamente s mudanas do mercado e s necessidades dos clientes.</p>
          <img className="float-right" src={require('../../../img/home2/inbox.png')} alt="" />
        </div>
        <div className="service_item">
          <div className="icon s_icon_three"><i className="ti-search"></i></div>
          <h4 className="f_600 f_size_20 l_height28 t_color2 mb_20">Segurana e Conformidade<br />Proteja Seu Negcio</h4>
          <p>Garanta a segurana das suas aplicaes web e a conformidade com regulamentos. Proteja os dados da sua empresa e dos seus clientes contra ameaas digitais.</p>
          <img className="float-right" src={require('../../../img/home2/file.png')} alt="" />
        </div>
        <div className="service_item">
          <div className="icon s_icon_four"><i className="ti-stats-up"></i></div>
          <h4 className="f_600 f_size_20 l_height28 t_color2 mb_20">Suporte e Manuteno<br />Operao Ininterrupta</h4>
          <p>Assegure a operao ininterrupta das suas aplicaes web com suporte e manuteno proativos. Mantenha seu negcio sempre  frente, sem interrupes.</p>
          <img className="float-right" src={require('../../../img/home2/report.png')} alt="" />
        </div>
      </Slider>
    )
  }
}
export default Pserviceslider;